import SwitchSizes from "../switch/SwitchSizes"
import { ChevronDown } from "react-feather"


const colourOptions = [
  { value: "ocean", label: "עוף", color: "#00B8D9", isFixed: true },
  { value: "blue", label: "דגים", color: "#0052CC", isFixed: true },
  { value: "purple", label: "חלב", color: "#5243AA", isFixed: true },
  { value: "red", label: "אורז", color: "#FF5630", isFixed: false },
  { value: "orange", label: "בשר", color: "#FF8B00", isFixed: false },
  { value: "yellow", label: "ירקות", color: "#FFC400", isFixed: false }
]

const category = [
  { value: "ocean", label: "חלבי", color: "#00B8D9", isFixed: true },
  { value: "blue", label: "בשרי", color: "#0052CC", isFixed: true },
  { value: "purple", label: "טבעוני", color: "#5243AA", isFixed: true }
]

const groupedOptions = [
  {
    label: "Ice Creams",
    options: [
      { value: "vanilla", label: "Vanilla" },
      { value: "Dark Chocolate", label: "Dark Chocolate" },
      { value: "chocolate", label: "Chocolate" },
      { value: "strawberry", label: "Strawberry" },
      { value: "salted-caramel", label: "Salted Caramel" }
    ]
  },
  {
    label: "Snacks",
    options: [
      { value: "Pizza", label: "Pizza" },
      { value: "Burger", label: "Burger" },
      { value: "Pasta", label: "Pasta" },
      { value: "Pretzel", label: "Pretzel" },
      { value: "Popcorn", label: "Popcorn" }
    ]
  }
]

const animatedComponents = makeAnimated()

const colourStyles = {
  control: styles => ({ ...styles, backgroundColor: "white" }),
  option: (styles, { data, isDisabled, isFocused, isSelected }) => {
    const color = chroma(data.color)
    return {
      ...styles,
      backgroundColor: isDisabled
        ? null
        : isSelected
        ? data.color
        : isFocused
        ? color.alpha(0.1).css()
        : null,
      color: isDisabled
        ? "#ccc"
        : isSelected
        ? chroma.contrast(color, "white") > 2
          ? "white"
          : "black"
        : data.color,
      cursor: isDisabled ? "not-allowed" : "default",

      ":active": {
        ...styles[":active"],
        backgroundColor:
          !isDisabled && (isSelected ? data.color : color.alpha(0.3).css())
      }
    }
  },
  multiValue: (styles, { data }) => {
    const color = chroma(data.color)
    return {
      ...styles,
      backgroundColor: color.alpha(0.1).css()
    }
  },
  multiValueLabel: (styles, { data }) => ({
    ...styles,
    color: data.color
  }),
  multiValueRemove: (styles, { data }) => ({
    ...styles,
    color: data.color,
    ":hover": {
      backgroundColor: data.color,
      color: "white"
    }
  })
}

const styles = {
  multiValue: (base, state) => {
    return state.data.isFixed ? { ...base, opacity: "0.5" } : base
  },
  multiValueLabel: (base, state) => {
    return state.data.isFixed
      ? { ...base, color: "#626262", paddingRight: 6 }
      : base
  },
  multiValueRemove: (base, state) => {
    return state.data.isFixed ? { ...base, display: "none" } : base
  }
}

const orderOptions = values => {
  if (values.length > 0)
    return values.filter(v => v.isFixed).concat(values.filter(v => !v.isFixed))
}

const formatGroupLabel = data => (
  <div className="d-flex justify-content-between align-center">
    <strong>
      {" "}
      <span>{data.label}</span>
    </strong>
    <span>{data.options.length}</span>
  </div>
)

class SelectOptions extends React.Component {
  state = {
    value: orderOptions([colourOptions[0], colourOptions[1], colourOptions[3]])
  }

  handleChange = newValue => {
    colourOptions.concat(newValue)
  }
  handleInputChange = () => {
    console.group("Input Changed")
    console.groupEnd()
  }

  fixedOnChange = (value, { action, removedValue }) => {
    switch (action) {
      case "remove-value":
      case "pop-value":
        if (removedValue.isFixed) {
          return
        }
        break
      case "clear":
        value = colourOptions.filter(v => v.isFixed)
        break
      default:
        return
    }

    value = orderOptions(value)
    this.setState({ value: value })
  }

  render() {
    return (
      <Card>
        <CardHeader>
          <CardTitle>שלום אורח/ת, הוספ/י מתכון כאורח/ת או <a href="">התחבר/י</a></CardTitle>
        </CardHeader>
        <CardBody>
          <Row>
            <Col md="6" sm="12">
              <h5 className="text-bold-600 my-1">בחר/י מוצרים</h5>
              <Select
                defaultValue={[colourOptions[2], colourOptions[3]]}
                isMulti
                name="colors"
                options={colourOptions}
                className="React"
                classNamePrefix="select"
              />
              </Col>
              <Col md="6" sm="12">
              <br/><br/>
              <FormGroup>
                    <InputGroup>
                      <Input placeholder="לא חובה" />
                      <InputGroupAddon addonType="append">
                        <InputGroupText>הוספת קישור לתמונה</InputGroupText>
                      </InputGroupAddon>
                    </InputGroup>
                  </FormGroup>
              </Col>
              </Row>
              <br/>
              <Row>
              <Col md="6" sm="12">
                          <Input
            type="textarea"
            name="text"
            id="exampleText"
            rows="3"
            placeholder="אופן הכנה"
          />
            </Col>
            <Col md="6" sm="12">

<Select
                className="React"
                classNamePrefix="select"
                defaultValue={category[0]}
                name="color"
                options={category}
              />
            </Col>
            <Col md="6" sm="12">
            <Button.Ripple color="primary">הוספה</Button.Ripple>

            </Col>
            </Row>
            <Row>
            </Row>
        </CardBody>
      </Card>
    )
  }
}
export default SelectOptions
